.hotelContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 55px;
  }

  .hotelWrapper {
    width: 100%;
    max-width: 1024px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
  }
  .TipoDieta{
    font-size: 20px;
  }
  .bookNow {
    position: absolute;
    top: 10px;
    right: 0;
    border: none;
    padding: 10px 20px;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
  }

  .hotelTitle {
    font-size: 24px;
  }

  .hotelAddress {
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .hotelDistance {
    color: #0071c2;
    font-weight: 500;
  }

  .hotelPriceHighlight {
    color: #008009;
    font-weight: 500;
    margin-right: 20px;
  }

  .hotelImages {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

  }

  .hotelImgWrapper {
    width: 90%;
  }

  .hotelImg {
    width: 100%;

  }

  .hotelDetails {
    display: flex;
    justify-content: space-between;

    margin-top: 20px;
  }

  .hotelDetailsTexts {
    flex: 3;
    text-align: initial;
  }

  .hotelDesc {
    font-size: 14px;
    margin-top: 20px;
  }

  .hotelDetailsPrice {
    flex: 1;
    background-color: #ebf3ff;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .hotelDetailsPrice > h1 {
    font-size: 18px;
    color: #555;
  }
  .hotelDetailsPrice > span {
    font-size: 14px;
  }
  .hotelDetailsPrice > h2 {
    font-weight: 300;
  }
  .hotelDetailsPrice > button {
    border: none;
    padding: 10px 20px;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
  }